//base64
function urlTobase64(url) {
	





















	
	

	return new Promise((resolve, reject) => {
		var toBase64Url;
		uni.request({
			url: url,
			method: 'GET',
			responseType: 'arraybuffer',
			success: res => {
				let base64 = uni.arrayBufferToBase64(res.data); //arraybufferbase64
				var toBase64Url = 'data:image/jpeg;base64,' + base64; //
				resolve(toBase64Url);
			},fail:err=>{
				console.log('base64--error--'+JSON.stringify(err));
			}
		});
	})

}

