/**
 * app 
 * type:   :    0:   1    2    3    4     5:
 * scene   :    WXSceneSession:     WXSenceTimeline:     WXSceneFavorite:
 */
function shareYzkkToWx(option) {
	return new Promise((resolve, reject) => {
		let sceneList = ['WXSceneSession', 'WXSenceTimeline', 'WXSceneFavorite']
		uni.share({
			provider: "weixin",
			scene: sceneList[option.scene],
			type: option.type || 0,
			href: option.link,
			title: option.title,
			summary: option.desc,
			imageUrl: option.shareImg || '',
			success: function(res) {
				console.log("success:" + JSON.stringify(res));
				resolve(res)
			},
			fail: function(err) {
				console.log("fail:" + JSON.stringify(err));
				reject(err)
			}
		});
	})
}

/**
 *     ['aa','b','c','cc','db','e']  ['c', 'b']  ===>  ["aa", "cc", "db", "e"]
 * @param {Object} a  ['aa','b','c','cc','db','e']
 * @param {Object} b  ['c', 'b']
 */
function noSame(a,b){
    return a.filter(c=>b.findIndex(d=>d===c)===-1)
}



