import commonJs from './common.js'
/**
 * header
 */
function getCommonHeader(imgUpload) {
	let header;
	if (imgUpload) {
		header = {
			'Content-type': 'multipart/form-data'
		};
	} else {
		header = {
			'Content-type': 'application/json;charset=UTF-8'
		};
	}

	// token
	let token = uni.getStorageSync("accesstoken");
	// let token = "eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxMzA3Nzg4NTQ5OSIsImNyZWF0ZWQiOjE1NzYyMjY5OTQ0NzgsImV4cCI6MTU3NjMxMzM5NH0.KSRIiSiFtdUkXFF7XTUR-ueaOmdZfW93M5QuuVoPW2nIZ5DNlEZpLp6_9lvIrL2MW1LmZnOdrTTKo1u2NlYEDQ";
	header = {
		'accesstoken': token || '0',







		'equipment': 'xcx',

	}
	return header;
};


/** 
 * 
 * config['noLoginCanSee'] :       resolve('seeOther')
 */
function request(url, data = {}, method = "POST", config = {}, timeout = 10000) {
	// header 
	let _header = getCommonHeader();

	return new Promise((resolve, reject) => {
		uni.request({
			url: url,
			data: data,
			timeout: timeout,
			header: _header,
			method: method,
			success: (res => {
				// 
				var statusCode=res.statusCode;
				// data
				var result = res.data;
				//  200 
				if (statusCode == 200) {
					if(result.status!=1){
						uni.showToast({
							title: result.message,
							icon: 'none'
						});                              
						return;
					}
				} else if (statusCode == 401){
					if(config.noLoginCanSee){
						resolve('seeOther');
					}else{
						uni.setStorageSync('userInfo', { 
							defaultHeadImg: "/static/images/icon/defLogo.png"
						});
						uni.showToast({
							title: result.message,
							icon: 'none'
						});
						uni.navigateTo({
							url: '/pages/package_other/login'
						})
						return;
					}
				}else if(statusCode == 404){
					uni.showToast({
						title: '--404',
						icon: 'none'
					});
					return;
				}
				resolve(result.data);
			}),
			fail: (err => {
				if (err.errMsg == "request:fail timeout" || err.errMsg.includes("")) {
					uni.showToast({
						title: '',
						icon: 'none'
					});
				}else if(err.errMsg == "request:fail"){
					// uni.showToast({
					// 	title: '--'+err.errMsg,
					// 	icon: 'none'
					// });
				}
				reject(err);
				return;
			})
		});
	});

};

/**
 * 
 * @param {Object} s
 */
function checkCode(s) {
	return new Promise((resolve, reject) => {
		switch (Number(s)) {
			case 1:
				uni.showToast({
					title: ' ' + result.status + ' ',
					icon: 'none'
				});
				resolve(result.data);
				return;
				break;
			case 401:
				uni.setStorageSync('userInfo', {
					defaultHeadImg: "/static/images/icon/defLogo.png"
				});
				uni.navigateTo({
					url: '/pages/package_other/login'
				})
				return;
				break;
			default:
				break;
		}

	})
}


/**
 * get 
 */
function getRequest(url, data = {}, config = {}) {
	return request(url, data, "GET", config);
}

/**
 * post  
 */
function postRequest(url, data = {}, config = {}, timeout) {
	return request(url, data, "POST", config, timeout);
}

/**
 * 
 */

function ajaxUploadFile(url, tempFilePaths, config = {}) {
	let headerConfig = getCommonHeader(1);
	return new Promise((resolve, reject) => {
		console.log('' + tempFilePaths);
		var uploadTask = uni.uploadFile({
			url: url,
			filePath: tempFilePaths.file,
			name: 'file',
			header: headerConfig,
			success: (res => {
				//  200 
				if (res.status && res.status != 200) {
					uni.showToast({
						title: ' ' + res.status + ' ',
						icon: 'none'
					});
					resolve(res);
					return;
				} else if (res.data.code == 2001) { //token
					uni.reLaunch({
						url: '/pages/package_other/login/index',
					})
				}
				resolve(res.data);
			}),
			fail: (err => {
				if (err.errMsg.indexOf('url not in domain list') > -1) {
					uni.showToast({
						title: 'url',
						icon: 'none'
					});
				}
				reject(err);
			})
		});
		uploadTask.onProgressUpdate((res) => {
			console.log('', res.progress)
		})
	});
}



export {
	getCommonHeader,
	request,
	getRequest,
	postRequest,
	ajaxUploadFile
}
